<template>
	<view>
		<mine-head ref="mineHead" />
		<mine-function  />

	</view>
</template>

<script>
	import utils from '@/utils/Utils.js'
	import api from '@/http/ApiService.js'
	import dataManager from '@/manager/DataManager.js'
	import bus from '@/manager/BusManager.js'
	var self
	export default {
		data() {
			return {
				userName: '请登录',
				collectNum: 0,
				coinNum: 0,
				rankNum: 0,
				show: false,
			};
		},
		onLoad() {
			self = this
			if (utils.isLogined()) {
				let name = dataManager.getUser().nickname
				if (!utils.isEmpty(name)) self.userName = name
			}
			self.getCoinInfo()
			bus.onLogin(function() {
				let name = dataManager.getUser().nickname
				if (!utils.isEmpty(name)) self.userName = name
				self.getCoinInfo()
			})
			bus.onCollect(function() {
				self.getCoinInfo()
			})
		},
		methods: {
			getCoinInfo() {
				api.getCoinInfo().then(res => {
					console.log(res)
					self.$refs.mineHead.setHead(res)
			         console.log(res)
					// self.collectNum = res[0].total
					// self.coinNum = res[1].coinCount
					// self.rankNum = res[1].rank
				})
			},
		}
	}
</script>

